<template>
  <el-row type="flex" justify="center">
    <el-col class="resultContainer" v-loading="loading" :span="18">
      <div v-if="hasFinalResult">
        <el-tooltip placement="bottom">
          <span slot="content">复制{{this.copied ? "成功": ""}}</span>
          <h3 @click="copyRst">识别结果: {{this.recognizeResult.join(" ")}}</h3>
        </el-tooltip>
      </div>
      <h3 v-else>请点击下方的操作按钮</h3>
      <el-row type="flex" justify="center">
        <el-col :span="3">
          <el-upload action :show-file-list="false" :auto-upload="false" @change.native="uploadImg">
            <el-button type="info" slot="trigger">选择图片</el-button>
          </el-upload>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="detect" :disabled="!hasPicture">检测卡号区域</el-button>
        </el-col>
        <el-col :span="3">
          <el-button type="success" @click="uploadCropImg" :disabled="!hasCropped">识别卡号</el-button>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" v-show="hasPicture">
        <el-col :span="18">
          <div id="cropBox">
            <vue-cropper
              ref="cropper"
              :guides="true"
              :auto-crop="true"
              :auto-crop-area="0.95"
              :rotatable="true"
              :background="true"
              :img-style="{'width': '640px', 'height': '480px'}"
              :src="this.curImgSrc"
            ></vue-cropper>
          </div>
        </el-col>
        <el-col :span="3">
          <el-row style="margin-top:250px">
          <el-col :span="18"><el-input v-model="rotateDegree" placeholder="旋转角度" inline></el-input></el-col>
          <el-col :span="3">
          <el-button
            type="warning"
            @click="rotate"
            icon="el-icon-refresh-right"
            circle
          ></el-button>
          </el-col>
          </el-row>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>
<script>
import { detectUrl, recognizeUrl } from "@/settings.js";
import VueCropper from "vue-cropperjs";
import axios from "axios";

const b64Regex = /^.*base64,?(.*)$/;

export default {
  props: {
    imgSrc: {
      default: require("@/assets/7.jpeg")
    },
    uploadActionUrl: {
      default: ""
    },
    subject: {
      default: ""
    }
  },
  data() {
    return {
      imgStyle: {
        height: "300px"
      },
      loading: false,
      curImgSrc: null,
      imageHeight: null,
      imageWidth: null,
      recognizeResult: [],
      clipboard: null,
      copied: false,
      rotateDegree: 90,
      hasCropped: false
    };
  },
  computed: {
    hasPicture() {
      return this.curImgSrc !== null;
    },
    hasFinalResult() {
      return this.recognizeResult.length > 0;
    }
  },
  methods: {
    detect() {
      let dataUrl = this.$refs["cropper"].getCroppedCanvas().toDataURL();
      let b64Content = b64Regex.exec(dataUrl);
      if (b64Content === null) {
        return;
      }

      let imgData = this.$refs["cropper"].getCropBoxData();
      let width = imgData["width"];
      let height = imgData["height"];
      let originX = imgData["left"]
      let originY = imgData["top"]
      console.log(width, height);
      this.loading = true;
      axios
        .post(detectUrl, { b64: b64Content[1] })
        .then(rst => {
          let { xmin, ymin, xmax, ymax, score } = rst.data;
          console.log(rst.data);
          let targetH = (ymax - ymin) * height;
          let targetW = (xmax - xmin) * width;
          xmin = ~~(xmin * width);
          ymin = ~~(ymin * height);
          this.$refs["cropper"].setCropBoxData({
            left: xmin+originX,
            top: ymin+originY,
            height: targetH,
            width: targetW
          });
          this.loading = false;
          this.copied = false;
          this.hasCropped = true;
        })
        .catch(err => {
          console.log(err);
          this.$message.error('识别失败');
          this.loading = false;
        });
    },
    uploadImg(event) {
      let file = event.target.files[0];
      console.log(file);
      if (!file.type.includes("image")) {
        this.$message.warning("请选择图片文件后再进行上传");
      } else {
        if (typeof FileReader === "function") {
          let reader = new FileReader();
          reader.onload = e => {
            let newImgSrc = e.target.result;
            this.curImgSrc = newImgSrc;
            this.$refs["cropper"].replace(newImgSrc);
            this.hasCropped = false;
          };
          reader.readAsDataURL(file);
        } else {
          this.$message.error("暂不支持的浏览器");
        }
      }
    },
    rotate() {
      this.$refs["cropper"].rotate(this.rotateDegree);
      let canvasData = this.$refs["cropper"].getCanvasData();
      console.log(this.$refs["cropper"].getCanvasData());
      this.$refs["cropper"].setCropBoxData({
        left: canvasData["left"],
        top: canvasData["top"],
        width: canvasData["width"],
        height: canvasData["height"]
      });
    },
    onCropMove() {
      // console.log(this.$refs["cropper"].getData());
    },
    uploadCropImg() {
      let dataUrl = this.$refs["cropper"].getCroppedCanvas().toDataURL();
      let content = b64Regex.exec(dataUrl);
      if (content.length > 0) {
        console.log(content[0]);
        axios({ method: "post", url: recognizeUrl, data: { b64: content[1] } })
          .then(res => {
            let resData = res.data;
            this.recognizeResult = resData["result"];
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    copyRst() {
      let text = this.recognizeResult.join("");
      this.$copyText(text)
        .then(() => (this.copied = true))
        .catch(() => {
          this.$message.error("复制失败");
        });
    },
  },
  components: {
    "vue-cropper": VueCropper
  },
  created() {}
};
</script>

<style>
#cropBox {
  margin-top: 10px;
  height: 500px;
  width: 700px;
}
</style>
